import React from 'react';
import { useParams } from "react-router-dom";

import { Footer, Header, MenuStrip, ScrollToTop } from '../components';

import { JoshutoProject, SpaceFarmerBotProject, RuijiProject } from '../components/Projects';

const ProjectsArray = [
	JoshutoProject,
	RuijiProject,
	SpaceFarmerBotProject,
];

function ProjectDetail(props) {
	const bookmarks = [ ];
	let { id } = useParams();

	if (id >= ProjectsArray.length) {
		return null;
	}

	const Content = ProjectsArray[id];

	return (
<div>
<Header {...props}/>
<div className="article">
	<MenuStrip bookmarks={bookmarks}/>
	<div className="article-content">
	<Content/>
	</div>
</div>
<Footer/>
<ScrollToTop/>
</div>
	);
}

export { ProjectDetail };

export default ProjectDetail;
